import { OrderTotalPriceWrapper } from './styled';

const OrderTotalPrice = ({ totalPrice }) => {
  const convertedTotalPrice = totalPrice
    .toString()
    .replace(/\B(?=(\d{3})+(?!\d))/g, ',');

  return (
    <OrderTotalPriceWrapper>
       <strong>{convertedTotalPrice}</strong>
    </OrderTotalPriceWrapper>
  );
};

export default OrderTotalPrice;
