/* eslint-disable no-param-reassign */
import { FinalPaymentWrapper, OrderPaymentWrapper } from './styled';
import { Button } from '../../common/Button/Button';
import { PaymentTextWrapper } from '../../common/Input/InputWrapper/PaymentTextWrapper';

const OrderPayment = ({
  data,
  onClick,
  onSubmit,
  setIsCheckBox,
  isCheckBox,
  errors,
}) => {
  const convertedTotalPrice = data
    .reduce((acc, cur) => {
      acc += cur.price * cur.quantity;
      acc += cur.shipping_fee;

      return acc;
    }, 0)
    .toString()
    .replace(/\B(?=(\d{3})+(?!\d))/g, ',');

  const convertedTotalShippingFee = data
    .reduce((acc, cur) => {
      acc += cur.shipping_fee;

      return acc;
    }, 0)
    .toString()
    .replace(/\B(?=(\d{3})+(?!\d))/g, ',');

  const convertedProductPrice = data
    .reduce((acc, cur) => {
      acc += cur.price * cur.quantity;

      return acc;
    }, 0)
    .toString()
    .replace(/\B(?=(\d{3})+(?!\d))/g, ',');

  return (
    <OrderPaymentWrapper>
      <h3 className="hidden"> </h3>
      <div className="payment-how">
        <p className="payment-title"></p>
        <PaymentTextWrapper>
          <input
            type={'radio'}
            name="payment_method"
            id="CARD"
            onClick={onClick}
          />
          <label htmlFor="CARD">/</label>
          <input
            type={'radio'}
            name="payment_method"
            id="DEPOSIT"
            onClick={onClick}
          />
          <label htmlFor="DEPOSIT"> </label>
          <input
            type={'radio'}
            name="payment_method"
            id="PHONE_PAYMENT"
            onClick={onClick}
          />
          <label htmlFor="PHONE_PAYMENT"> </label>
          <input
            type={'radio'}
            name="payment_method"
            id="NAVERPAY"
            onClick={onClick}
          />
          <label htmlFor="NAVERPAY"> </label>
          <input
            type={'radio'}
            name="payment_method"
            id="KAKAOPAY"
            onClick={onClick}
          />
          <label htmlFor="KAKAOPAY"> </label>
        </PaymentTextWrapper>
        {errors.payment_method && (
          <p className="errors-message">{errors.payment_method}</p>
        )}
      </div>
      <div className="payment-result-box">
        <p> </p>
        <FinalPaymentWrapper>
          <p>
            - 
            <strong>{convertedProductPrice}</strong>
            <span></span>
          </p>
          <p>
            - 
            <strong>0</strong>
            <span></span>
          </p>
          <p>
            - 
            <strong>{convertedTotalShippingFee}</strong>
            <span></span>
          </p>
          <p className="total-price">
            - 
            <strong>{convertedTotalPrice}</strong>
          </p>
          <div className="consent">
            <input
              type="checkbox"
              name="consent"
              id="consent"
              onClick={() => {
                setIsCheckBox((prev) => !prev);
              }}
            />
            <label htmlFor="consent">
                ,     .
            </label>
            <Button
              size="large"
              type="submit"
              form="payment-submit"
              onClick={onSubmit}
              disabled={isCheckBox}
            >
              
            </Button>
          </div>
        </FinalPaymentWrapper>
      </div>
    </OrderPaymentWrapper>
  );
};
export default OrderPayment;
