import { OrderItemWrapper } from './styled';

const OrderItem = ({ data }) => {
  const convetedShippingFee = data.shipping_fee
    .toString()
    .replace(/\B(?=(\d{3})+(?!\d))/g, ',');
  const convetedPrice = (data.price * data.quantity)
    .toString()
    .replace(/\B(?=(\d{3})+(?!\d))/g, ',');
  return (
    <OrderItemWrapper>
      <li>
        <img src={data.image} alt="  " />
      </li>
      <li className="text-box">
        <p className="store-name">{data.store_name}</p>
        <p className="product-name">{data.product_name}</p>
        <p className="product-quantity"> : {data.quantity}</p>
      </li>
      <li className="sale">-</li>
      <li className="delivery">
        {data.shipping_fee ? `${convetedShippingFee} ` : ''}
      </li>
      <li className="product-price">{convetedPrice}</li>
    </OrderItemWrapper>
  );
};

export default OrderItem;
